import { MaterialIcons } from "@expo/vector-icons";
import React, { useCallback, useContext, useMemo, useState } from "react";
import {
  FlatList,
  ListRenderItemInfo,
  NativeScrollEvent,
  NativeSyntheticEvent,
  StyleSheet,
  TouchableOpacity,
  View,
} from "react-native";
import { useTheme } from "react-native-elements";
import { getEndingData } from "../../backend/endings/Endings";
import { hasFlag, hasGlobalFlag } from "../../backend/Flags";
import { isInventoryUnlocked } from "../../backend/items/Items";
import { PlayerContext } from "../../backend/PlayerContext";
import { getTotalElementAndSchoolExp } from "../../backend/spells/ElementsAndSchools";
import Divider from "../utility/Divider";
import { GameIcon } from "../utility/icons/GameIcon";
import { Text } from "../utility/Text";

const SelectedContext = React.createContext("Campus");

function MenuComponent(props: { title: string; onPress: () => void }) {
  const { title, onPress } = { ...props };
  const theme = useTheme();
  const selected = useContext(SelectedContext);
  const shouldHighlight = selected == title;
  return useMemo(
    () => (
      <TouchableOpacity onPress={onPress}>
        <View
          style={[
            styles.itemContainerStyle,
            shouldHighlight
              ? { backgroundColor: theme.theme.colors.grey5 }
              : null,
          ]}
        >
          <GameIcon icon={title.toLowerCase()} />
          <Text style={styles.itemStyle}>{title}</Text>
        </View>
      </TouchableOpacity>
    ),
    [title, onPress, shouldHighlight, theme],
  );
}

const SCROLL_VIEW_EPSILON = 10;

export function BottomMenu(props: {
  onNavigationPress: (section: string) => void;
  onSpellMenuPress: () => void;
  selected: string;
}) {
  const playerContext = useContext(PlayerContext);

  const showResearch = hasFlag(playerContext, "research_unlocked");
  const showAutocast = hasFlag(playerContext, "autocast_unlocked");
  const showInventory = isInventoryUnlocked(playerContext);
  const showExploration = hasFlag(playerContext, "exploration_unlocked");
  const showRetirement = getTotalElementAndSchoolExp(playerContext) >= 10000;
  const showUpgrades = hasFlag(playerContext, "upgrades_unlocked");
  const showCreatures = hasFlag(playerContext, "creatures_unlocked");
  const showStrengthening =
    hasGlobalFlag(playerContext, "strengthening_unlocked") && showExploration;
  const showEnding = getEndingData(playerContext) != null;

  const navigationPressCallback = useCallback(
    (item: string) => {
      props.onNavigationPress(item);
    },
    [props.onNavigationPress],
  );

  const menuItems = useMemo(
    () =>
      [
        "Campus",
        showResearch ? "Research" : null,
        showAutocast ? "Apprentices" : null,
        showInventory ? "Inventory" : null,
        showExploration ? "Exploration" : null,
        showCreatures ? "Creatures" : null,
        showUpgrades ? "Upgrades" : null,
        showStrengthening ? "Strengthen" : null,
        showEnding ? "Ending" : null,
        showRetirement ? "Retirement" : null,
        "Stats",
        "Help",
        "Options",
        "About",
      ].filter((value) => value != null) as string[],
    [
      showResearch,
      showAutocast,
      showInventory,
      showExploration,
      showCreatures,
      showUpgrades,
      showStrengthening,
      showEnding,
      showRetirement,
    ],
  );
  const callbacks = useMemo(
    () => menuItems.map((item) => () => navigationPressCallback(item)),
    [menuItems, navigationPressCallback],
  );

  const renderItem = useCallback(
    (item: ListRenderItemInfo<string>) => (
      <View style={{ flexDirection: "row" }}>
        {item.index === 0 && <Divider orientation="vertical" />}
        <MenuComponent title={item.item} onPress={callbacks[item.index]} />
        <Divider orientation="vertical" />
      </View>
    ),
    [callbacks, navigationPressCallback],
  );

  const keyExtractor = useCallback((item: string) => item, []);

  const theme = useTheme();
  const [scrollEnd, setScrollEnd] = useState(false);
  const [scrollStart, setScrollStart] = useState(true);
  const onScroll = useCallback(
    (e: NativeSyntheticEvent<NativeScrollEvent>) => {
      const position = e.nativeEvent.contentOffset.x;
      const contentWidth = e.nativeEvent.contentSize.width;
      const scrollViewWidth = e.nativeEvent.layoutMeasurement.width;
      const isStart = position < SCROLL_VIEW_EPSILON;
      const isEnd =
        contentWidth - scrollViewWidth - position < SCROLL_VIEW_EPSILON;
      if (isStart != scrollStart) {
        setScrollStart(isStart);
      }
      if (isEnd != scrollEnd) {
        setScrollEnd(isEnd);
      }
    },
    [scrollEnd, scrollStart, setScrollEnd, setScrollStart],
  );

  const flatListBackgroundStyle = useMemo(
    () => [
      styles.scrollContentContainer,
      { backgroundColor: theme.theme.colors.background },
    ],
    [theme],
  );

  return useMemo(
    () => (
      <View
        style={[
          styles.bottomMenuContainer,
          { backgroundColor: theme.theme.colors.grey5 },
        ]}
      >
        <View style={[styles.chevron, scrollStart && { opacity: 0 }]}>
          <MaterialIcons
            name="chevron-left"
            size={24}
            color={theme.theme.colors.black}
          />
        </View>
        <SelectedContext.Provider value={props.selected}>
          <FlatList
            data={menuItems}
            renderItem={renderItem}
            keyExtractor={keyExtractor}
            horizontal={true}
            snapToInterval={71}
            style={styles.scrollContainer}
            contentContainerStyle={flatListBackgroundStyle}
            scrollEventThrottle={100}
            onScroll={onScroll}
            overScrollMode="never"
            showsHorizontalScrollIndicator={false}
            nestedScrollEnabled={true}
          />
        </SelectedContext.Provider>
        <View style={[styles.chevron, scrollEnd && { opacity: 0 }]}>
          <MaterialIcons
            name="chevron-right"
            size={24}
            color={theme.theme.colors.black}
          />
        </View>
        <View style={styles.spellButtonPlaceholder} />
      </View>
    ),
    [
      menuItems,
      renderItem,
      theme,
      scrollStart,
      scrollEnd,
      flatListBackgroundStyle,
      onScroll,
      props.selected,
    ],
  );
}

const styles = StyleSheet.create({
  scrollContainer: {
    flex: 1,
    height: 48,
  },
  scrollContentContainer: {
    paddingVertical: 0,
    justifyContent: "center",
  },
  navigationBlock: {
    flexDirection: "row",
    alignItems: "stretch",
  },
  itemContainerStyle: {
    width: 70,
    alignItems: "center",
    flex: 1,
    justifyContent: "center",
  },
  itemStyle: {
    fontSize: 10,
  },
  gradient: {
    flex: 1,
  },
  spellButtonPlaceholder: {
    width: 84,
    height: 48,
  },
  bottomMenuContainer: { flexDirection: "row", alignItems: "center" },
  spellMenuButtonPlaceholder: {
    height: 48,
    justifyContent: "flex-end",
    alignItems: "flex-end",
    overflow: "visible",
  },
  chevron: {
    width: 24,
    height: 48,
    justifyContent: "center",
  },
});
