import { ThemeMode } from "@rneui/themed";
import React from "react";
import { Platform, StyleSheet } from "react-native";
import { ThemeOptions, useTheme } from "react-native-elements";
import { color } from "react-native-elements/dist/config";
import { Directions, Gesture } from "react-native-gesture-handler";
import { SpellElement } from "../../backend/spells/ElementsAndSchools";
import { Markdown } from "./Markdown";

export function disabledColorByTheme(theme: ThemeOptions) {
  return theme.mode === "dark"
    ? color(theme?.colors?.disabled).lighten(1.2).string()
    : color(theme?.colors?.disabled).darken(0.3).string();
}

const LIGHT_ELEMENT_COLORS = {
  Fire: "#c30",
  Earth: "#740",
  Water: "#08f",
  Air: "#0b4",
  Nature: "#5b0",
  Lightning: "#80f",
  Darkness: "#444",
};

const DARK_ELEMENT_COLORS = {
  Fire: "#820",
  Earth: "#630",
  Water: "#048",
  Air: "#063",
  Nature: "#360",
  Lightning: "#408",
  Darkness: "#444",
};

export function showImportFromFile(): boolean {
  return Platform.select({
    ios: true,
    android: true,
    default: false,
  });
}

export function showExportToFile(): boolean {
  return Platform.select({
    ios: true,
    android: true,
    web: true,
    default: false,
  });
}

export function elementColorByTheme(
  themeMode: ThemeMode,
  element: SpellElement,
) {
  return themeMode === "dark"
    ? DARK_ELEMENT_COLORS[element]
    : LIGHT_ELEMENT_COLORS[element];
}

export function getPrimaryButtonTextColor(
  theme: ThemeOptions,
  disabled: boolean,
): string {
  if (disabled) {
    return disabledColorByTheme(theme);
  }
  return "white";
}

export function DescriptionViewer(props: any) {
  const theme = useTheme();
  return (
    <Markdown style={[{ color: theme.theme.colors.grey1 }, props.style || {}]}>
      {props.children}
    </Markdown>
  );
}

export function EffectViewer(props: any) {
  const theme = useTheme();
  return (
    <Markdown
      style={[
        styles.effectText,
        { color: theme.theme.colors.grey1 },
        props.style || {},
      ]}
    >
      {props.children}
    </Markdown>
  );
}

export function createGestureHandler(
  swipeLeftHandler: () => void,
  swipeRightHandler: () => void,
) {
  if (Platform.OS === "web") {
    const leftGesture = Gesture.Fling()
      .direction(Directions.LEFT)
      .onEnd((e: any, success) => {
        if (success && e.translationX < 0) {
          swipeLeftHandler();
        }
      });
    const rightGesture = Gesture.Fling()
      .direction(Directions.RIGHT)
      .onEnd((e: any, success) => {
        if (success && e.translationX > 0) {
          swipeRightHandler();
        }
      });

    return Gesture.Race(leftGesture, rightGesture);
  }

  let sharedTouchPosition = {
    x: 0,
    y: 0,
    initialX: 0,
    initialY: 0,
    initialTimestamp: 0,
    lastTimestamp: 0,
    status: "end",
  };
  const POSITION_THRESHOLD = 80;
  const VELOCITY_THRESHOLD = 300;
  const Y_POSITION_THRESHOLD = 65;
  return Gesture.Manual()
    .onTouchesDown((e, manager) => {
      if (sharedTouchPosition.status == "begin") {
        // Do nothing
        return;
      }

      sharedTouchPosition.initialX = e.allTouches[0].x;
      sharedTouchPosition.initialY = e.allTouches[0].y;

      sharedTouchPosition.x = e.allTouches[0].x;
      sharedTouchPosition.y = e.allTouches[0].y;

      sharedTouchPosition.initialTimestamp = Date.now();
      sharedTouchPosition.lastTimestamp = Date.now();
      sharedTouchPosition.status = "begin";
    })
    .onTouchesMove((e, manager) => {
      if (sharedTouchPosition.status != "begin") {
        return;
      }
      const currentTimestamp = Date.now();
      const timestampDeltaSec =
        Math.max(currentTimestamp - sharedTouchPosition.initialTimestamp, 1) /
        1000.0;
      const deltaX = e.allTouches[0].x - sharedTouchPosition.initialX;
      const deltaY = e.allTouches[0].y - sharedTouchPosition.initialY;
      const velocityX = deltaX / timestampDeltaSec;
      const velocityY = deltaY / timestampDeltaSec;
      const tooVertical = Math.abs(deltaY) > Y_POSITION_THRESHOLD;
      if (tooVertical) {
        sharedTouchPosition.status = "end";
        return;
      }

      if (
        Math.abs(deltaY) * 2.5 > Math.abs(deltaX) &&
        Math.abs(deltaX) > POSITION_THRESHOLD
      ) {
        sharedTouchPosition.status = "end";
        return;
      }

      if (
        velocityX < -VELOCITY_THRESHOLD &&
        e.allTouches[0].x - sharedTouchPosition.initialX < -POSITION_THRESHOLD
      ) {
        swipeLeftHandler();
        sharedTouchPosition.status = "end";
      } else if (
        velocityX > VELOCITY_THRESHOLD &&
        e.allTouches[0].x - sharedTouchPosition.initialX > POSITION_THRESHOLD
      ) {
        swipeRightHandler();
        sharedTouchPosition.status = "end";
      }
      sharedTouchPosition.x = e.allTouches[0].x;
      sharedTouchPosition.y = e.allTouches[0].y;
      sharedTouchPosition.lastTimestamp = currentTimestamp;
    })
    .onTouchesUp((e, manager) => {
      if (e.numberOfTouches === 0) {
        const currentTimestamp = Date.now();
        const timestampDeltaSec =
          Math.max(currentTimestamp - sharedTouchPosition.initialTimestamp, 1) /
          1000.0;
        const velocityX =
          (e.allTouches[0].x - sharedTouchPosition.initialX) /
          timestampDeltaSec;
        const velocityY =
          (e.allTouches[0].y - sharedTouchPosition.initialY) /
          timestampDeltaSec;
        const deltaX = e.allTouches[0].x - sharedTouchPosition.initialX;
        sharedTouchPosition.status = "end";
      }
    })
    .onTouchesCancelled((e, manager) => {
      sharedTouchPosition.status = "end";
    });
}

const styles = StyleSheet.create({
  effectText: {
    fontWeight: "bold",
  },
});
